
/*-------------------------------------------------------------------------------------------------------------------------------*/
/* GLOBAL SETTINGS */
/*-------------------------------------------------------------------------------------------------------------------------------*/

html {
	height: 100%;
}

body {
	height: 100%;
	overflow: hidden;
}

#content-block {
	overflow: hidden;
	position: relative;
}

.overflow-hidden {
	overflow: hidden;
}

html.overflow-hidden body {
	overflow: hidden;
}

/*-------------------------------------------------------------------------------------------------------------------------------*/
/* SP */
/*-------------------------------------------------------------------------------------------------------------------------------*/
.sp-top{
	&-240{
		padding-top: 240px;
	}
	&-200{
		padding-top: 200px;
	}
	&-100{
		padding-top: 100px;
	}
	&-75{
		padding-top: 75px;
	}
}
.sp-bt{
	&-100{
		padding-bottom: 100px;
	}
	&-55{
		padding-bottom: 55px;
	}
}
.mb{
	&-10{
		margin-bottom: 10px;
	}
	&-55{
		margin-bottom: 55px;
	}
	&-90{
		margin-bottom: 90px;
	}
}

/*-------------------------------------------------------------------------------------------------------------------------------*/
/* Section settings */
/*-------------------------------------------------------------------------------------------------------------------------------*/
.section {
	position: relative;
	z-index: 0;
	&.no-rl{
		position: static;
	}
	&.bg-orang{
		background-color: #FEF6EC;
	}
	&.bg-dark{
		background-color: #140D20;
	}
}
.container-cs{
	max-width: 1190px;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
}
.row{
	&.row-5{
		margin-right: -5px;
		margin-left: -5px;
		> [class*=col]{
			padding-right: 5px;
			padding-left: 5px;
		}
	}
}
/*-------------------------------------------------------------------------------------------------------------------------------*/
/* ELEMENTS */
/*-------------------------------------------------------------------------------------------------------------------------------*/
.video-bg:after,
.bg:after {
	content: '';
	display: block;
}

.video-bg, .video-bg:after, .wr-hd, .bg, .bg:after {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

.bg {
	background-size: cover;
	background-repeat: no-repeat;
}

.wr-hd {
	overflow: hidden;
	z-index: -1;
}

.bg.parallax-bg {
	height: 125%;
	top: -10%;
}

.video-bg video {
	position: absolute;
	top: 50%;
	left: 50%;
	min-width: 100%;
	min-height: 100%;
	width: auto;
	height: auto;
	-webkit-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%) scale(1.1);
}

.icon-arrow {
	position: relative;
	display: inline-block;
	width: 7px;
	height: 7px;
}

.icon-arrow::before, .icon-arrow::after {
	content: "";
	position: absolute;
	top: 0;
	width: 1px;
	height: 9px;
	background-color: #111111;
}

.icon-arrow::before {
	left: 0;
	transform: rotate(-45deg);
}

.icon-arrow::after {
	right: 0;
	transform: rotate(45deg);
}

#loader-wrapper {
	position: fixed;
	left: 0;
	top: -100px;
	right: 0;
	bottom: -100px;
	background: #ffffff;
	z-index: 2000;
}

.separator {
	border-bottom: 1px solid #dcecf2;
}

.image-wrapper img {
	max-width: 100%;
}

.text-gradient{
	-webkit-text-fill-color: rgba(1,1,1,0);
	-webkit-background-clip: text;
	background-image: linear-gradient(90deg, #FEAC5E 0%, #C779D0 50%, #4BC0C8 100%);
}
.title-max{
	max-width: 900px;
	&.center{
		margin-left: auto;
		margin-right: auto;
	}
}

