
/*==========  TYPOGRAPHY  ==========*/

body {
	font-family: $mainFont;
	color: $colorTextDark;
}

b, strong {
	font-weight: 700;
}

a:hover {
	text-decoration: none;
}

ul, ol {
	list-style: none;
}

body, .text {
	font-size: 18px;
	font-weight: 400;
	line-height: 1.3em;
}
.text-md{
	font-size: 16px;
	line-height: 1.3em;
}

.text-sm {
	font-size: 14px;
	line-height: 1.3em;
}

.text-lg {
	font-size: 20px;
	line-height: 1.4em;
}

.text-xl {
	font-size: 24px;
	line-height: 1.3em;
}

.text > * {
	margin-bottom: 30px;
}

.text > *:last-child {
	margin-bottom: 0;
}

.text img {
	max-width: 100%;
	height: auto;
	display: block;
}

.text ol {
	counter-reset: number;
}

.text ol li {
	padding-left: 0;
	margin-bottom: 15px;
	position: relative;
}

.text ol li:last-of-type {
	margin-bottom: 0;
}

.text ol li:before {
	counter-increment: number;
	content: "0" counter(number) ".";
	margin-right: 8px;
	font-weight: 700;
	font-size: 14px;
	color: #111111;
}

.text ul li {
	padding-left: 20px;
	margin-bottom: 15px;
	position: relative;
}

.text ul li:last-of-type {
	margin-bottom: 0;
}

.text ul li:before {
	content: "";
	display: inline-block;
	background-color: #111111;
	background-repeat: no-repeat;
	background-size: contain;
	width: 5px;
	height: 5px;
	vertical-align: middle;
	position: absolute;
	left: 0;
	top: 0.55em;
}

.text blockquote {
	color: #1f1f1f;
	font-size: 20px;
	padding: 5px 5px 5px 25px;
	border-left: 4px solid #00a2e0;
	font-weight: 600;
}

.text > iframe {
	width: 100%;
	height: 480px;
	max-height: calc(56.3vw - 30px);
}
.text{
	table{
		border-left: 1px solid #d1d1d1;
		border-top: 1px solid #d1d1d1;
		border-spacing: 0;
		text-align: left;
	}
	th, td{
		padding: 8px;
		border-right: 1px solid #d1d1d1;
		border-bottom: 1px solid #d1d1d1;
	}
}

.text b {
	font-weight: 700;
	color: #111111;
}

.text a{
	text-decoration: none;
	color: $colorDark;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
	font-family: $secondaryFont;
	font-weight: 500;
	color: $colorTextDark;
}

h1, .h1 {
	font-size: 64px;
	line-height: 1.2em;
}

h2, .h2 {
	font-size: 48px;
	line-height: 1.2em;
}

h3, .h3 {
	font-size: 40px;
	line-height: 1.2em;
}

h4, .h4 {
	font-size: 32px;
	line-height: 1.2em;
}

h5, .h5 {
	font-size: 24px;
	line-height: 1.4em;
}

h6, .h6 {
	font-size: 18px;
	line-height: 1.4em;
}

.text-left {
	text-align: left;
}

.text-center {
	text-align: center;
}

.text-right {
	text-align: right;
}

.fw-light {
	font-weight: 300;
}
.fw-regular{
	font-weight: 400;
}

.color-primary{
	color: $colorTextDark;
}
.color-secondary {
	color: $colorMain;
}
.color-light{
	color: #fff;
}
.color-op{
	color: rgba(1,1,1,0.6);
}
.color-op-light{
	color: rgba(255,255,255,0.6)
}
.color-light-t2{
	color: rgba(255,255,255,0.3)
}


/*==========  Hover  ==========*/

@media (min-width: 1200px){
	.text a:hover{
		opacity: 0.85;
	}
}


/*==========  MEDIA QUERIES  ==========*/

// @media (max-width: 1368px)

@media (max-width: 1199px){
	.text-lg{
		font-size: 18px;
	}
}

// @media (max-width: 991px)

@media (max-width: 768px){
	body, .text{
		font-size: 16px;
	}
	.text-xl{
		font-size: 18px;
	}
	.text-lg{
		font-size: 16px;
	}
	h1, .h1{
		font-size: 34px;
	}
	h2, .h2{
		font-size: 32px;
	}
	h3, .h3{
		font-size: 26px;
	}
	h4, .h4{
		font-size: 24px;
	}
	h5, .h5{
		font-size: 22px;
	}

}

