
/*==========  BUTTONS  ==========*/

.btn {
	display: inline-block;
	vertical-align: bottom;
	text-align: center;
	position: relative;
	cursor: pointer;
	text-decoration: none;
	border: none;
	padding: 0;
	background: none;
	outline: none;
}

.btn-primary {
	border-radius: 16px;
	font-weight: 500;
	padding: 16px 20px 17px 20px;
	font-size: 18px;
	background-color: $colorMain;
	color: #fff;
	transition: $transition;
	//&:active {
	//	transform: scale(.99);
	//}
	> span{
		vertical-align: middle;
	}
	.icon{
		display: inline-block;
		width: 24px;
		line-height: 0;
		margin-right: 8px;
		img{
			width: 100%;
		}
	}
	&.sm{
		padding: 12px 20px 13px 20px;
	}
	&.tx-sm{
		font-size: 16px;
	}
	&.tx-xl{
		font-size: 20px;
	}
	&.dark{
		background-color: $colorDark;
	}
	&.light{
		background-color: #fff;
		color: $colorTextDark;
	}
}

.btn-secondary {
	border-radius: 16px;
	font-weight: 500;
	padding: 16px 20px 18px 20px;
	font-size: 18px;
	background-color: rgba(1,1,1,0);
	color: $colorDark;
	transition: $transition;
	//&:active {
	//	transform: scale(.99);
	//}
	> span{
		vertical-align: middle;
	}
	.icon{
		display: inline-block;
		width: 24px;
		line-height: 0;
		margin-right: 8px;
		img{
			width: 100%;
		}
	}
}

.btn-round{
	padding: 14px 20px;
	background-color: #6e6e6e;
	color: #fff;
	font-size: 16px;
	line-height: 1.25em;
	font-weight: 500;
	border-radius: 82px;
}

.btn-link {
	font-weight: 700;
	color: #f6605c;
}

.btn-block {
	display: block;
	width: 100%;
}

.btn-close {
	width: 50px;
	height: 50px;
	font-size: 20px;
	line-height: 40px;
	background-color: #ffffff;
	position: relative;
	z-index: 1;
	cursor: pointer;
	outline: none;
	display: block;
	text-align: center;
	transition: all .37s ease-out;
	color: #4d4d4d;
}

.btn-wrapper{
	display: flex;
	align-items: center;
	&.left{
		justify-content: flex-start;
	}
	&.right{
		justify-content: flex-end;
	}
	&.center{
		justify-content: center;
	}
	> *{
		margin-right: 15px;
		&:last-child{
			margin-right: 0;
		}
	}
}

.btn-arrow{
	padding: 0 60px;
	position: relative;
	display: inline-block;
	&:after{
		content: '';
		width: 34px;
		height: 34px;
		display: block;
		background-image: url(../img/icons/icon-left-arrow.svg);
		background-repeat: no-repeat;
		background-size: contain;
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%);
	}
}

/*==========  Hover  ==========*/

@media (min-width: 992px){
	.btn-primary:hover:not(.dark):not(.light){
		background-color: #7161f3;
	}
	.btn-primary.light:hover{
		background-color: #f2f1fe;
	}
	.btn-primary.dark:hover{
		background-color: #2f2f2f;
	}
	.btn-secondary:hover{
		opacity: 0.85;
	}
}

/*==========  MEDIA QUERIES  ==========*/

// @media (max-width: 1368px)

// @media (max-width: 1199px)

// @media (max-width: 991px)

@media (max-width: 768px){
	.btn-primary{
		padding: 14px 16px 15px 16px;
		font-size: 16px;
		.icon{
			margin-right: 4px;
		}
		&.tx-xl{
			font-size: 18px;
		}
	}
	.btn-secondary{
		font-size: 16px;
		.icon{
			margin-right: 4px;
		}
	}
	.btn-arrow{
		padding: 0 46px;
		&:after{
			width: 28px;
			height: 28px;
		}
	}
}

