/*==========  Header Style  ==========*/
.header{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transition: background-color .28s ease-out;
  z-index: 4;
  &-inner{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 98px;
    padding: 5px 0;
    transition: all .28s ease-out;
    .logo{
      display: inline-block;
      width: 200px;
      line-height: 0;
      img{
        max-width: 100%;
      }
    }
    .nav{
      margin-left: auto;
      margin-right: 32px;
      li{
        display: inline-block;
        margin-right: 32px;
        &:last-of-type{
          margin-right: 0;
        }
        a{
          text-decoration: none;
          color: $colorTextDark;
          font-size: 18px;
          font-weight: 500;
          transition: $transition;
        }
        &.active{
          a{
            color: $colorRed;
          }
        }
      }
    }
  }
  &-burger{
    width: 40px;
    flex: 0 0 40px;
    height: 20px;
    position: relative;
    cursor: pointer;
    margin-left: 20px;
    z-index: 4;
    display: none;
    .close,
    .open{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      span{
        width: 40px;
        height: 2px;
        display: block;
        position: absolute;
        // background-color: $darkBlue;
        background-color: #111;
        backface-visibility: hidden;
        transition: 0.32s;
      }
    }
    .open{
      span{
        &:nth-child(1){
          top: 0;
          left: 0;
          transition-delay: 0.5s;
        }
        &:nth-child(2){
          top: 9px;
          right: 0;
          transition-delay: 0.55s;
        }
        &:nth-child(3){
          bottom: 0;
          left: 0;
          transition-delay: 0.6s;
        }
      }
    }
    .close{
      span{
        width: 0;
        &:nth-child(1){
          top: -6px;
          left: 6px;
          transform: rotate(45deg);
          transform-origin: left;
          transition-delay: 0s;
        }
        &:nth-child(2){
          top: -6px;
          right: 6px;
          transform: rotate(-45deg);
          transform-origin: right;
          transition-delay: 0.08s;
        }
      }
    }
    &.active{
      .close{
        span{
          width: 40px;
          &:nth-child(1){
            transition-delay: 0.5s;
          }
          &:nth-child(2){
            transition-delay: 0.58s;
          }
        }
      }
      .open{
        span{
          width: 0;
          &:nth-child(1){
            transition-delay: 0s;
          }
          &:nth-child(2){
            transition-delay: 0.05s;
          }
          &:nth-child(3){
            transition-delay: 0.1s;
          }
        }
      }
    }
  }
  &-layer{
    position: fixed;
    left: 0;
    right: 0;
    top: 68px;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: -1;
    -webkit-transition: 0.1s;
    transition: 0.1s;
    opacity: 0;
    visibility: hidden;
  }
}

@media (min-width: 992px) {
  .header{
    &.scrolled{
      background-color: rgba(255, 255, 255, 0.992);
      border-bottom: 1px solid rgba(1,1,1,0.04);
      .header-inner{
        height: 70px;
      }
    }
  }
}

/*==========  Hover  ==========*/

@media (min-width: 991px){
  .header-inner .nav li a:hover{
    color: $colorRed;
  }
}

/*==========  MEDIA QUERIES  ==========*/

// @media (max-width: 1368px){}

// @media (max-width: 1199px){}

@media (max-width: 991px){
  .header{
    background-color: rgba(255, 255, 255, 0.992);
    border-bottom: 1px solid rgba(1, 1, 1, 0.04);
    &-burger{
      display: block;
    }
    &-inner{
      height: 66px;
      .logo{
        width: 180px;
        margin-right: auto;
        position: relative;
        z-index: 2;
      }
      .nav{
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        max-width: 50%;
        background-color: #fff;
        text-align: center;
        margin-right: 0;
        padding-top: 120px;
        transition: all .36s ease-out;
        transform: translateX(100%);
        li{
          display: block;
          margin-right: 0;
          margin-bottom: 15px;
        }
      }
    }
    &.active{
      .header-layer{
        opacity: 1;
        visibility: visible;
      }
      .header-inner .nav{
        transform: none;
      }
    }
  }
}

@media (max-width: 768px){
  .header-inner .nav{
    max-width: 65%;
  }
}

@media (max-width: 575px){
  .header-inner .nav{
    max-width: 80%;
  }
}




