/*==========  Decor  ==========*/

.decor{
  display: inline-block;
  line-height: 0;
  position: absolute;
  z-index: -1;
  &.ps-01{
    top: 0;
    left: 0;
  }
  &.ps-02{
    top: 0;
    left: 50%;
    transform: translateX(-50%)
  }
  &.ps-03{
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)
  }
  &.ps-04{
    top: 0;
    right: 20%;
  }
}

