/*##########*/
/* FONTS */
/*##########*/
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap');

@font-face {
  font-family: "ClashDisplay";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/ClashDisplay/ClashDisplay-Regular/ClashDisplay-Regular.eot");
  src: url("../fonts/ClashDisplay/ClashDisplay-Regular/ClashDisplay-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/ClashDisplay/ClashDisplay-Regular/ClashDisplay-Regular.woff") format("woff"), url("../fonts/ClashDisplay/ClashDisplay-Regular/ClashDisplay-Regular.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "ClashDisplay";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/ClashDisplay/ClashDisplay-Medium/ClashDisplay-Medium.eot");
  src: url("../fonts/ClashDisplay/ClashDisplay-Medium/ClashDisplay-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/ClashDisplay/ClashDisplay-Medium/ClashDisplay-Medium.woff") format("woff"), url("../fonts/ClashDisplay/ClashDisplay-Medium/ClashDisplay-Medium.ttf") format("truetype");
  font-display: swap;
}
